.pet-details-container {
  max-width: 640px;
  margin: 2.4rem auto;
  padding: 2rem 2rem 2rem 2rem;
  border-radius: var(--radius-xl, 12px);
  background: var(--bg-white, #fff);
  box-shadow: var(--shadow-md, 0 4px 6px rgba(0,0,0,0.10));
}
.pet-details-card {
  display: flex;
  flex-direction: row;
  gap: 2.2rem;
  align-items: flex-start;
}
.pet-details-image-wrapper {
  min-width: 170px;
  min-height: 170px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6;
  border-radius: var(--radius-lg, 8px);
  overflow: hidden;
}
.pet-image {
  width: 170px;
  height: 170px;
  object-fit: cover;
  border-radius: var(--radius-lg, 8px);
}
.pet-details-main {
  flex: 1;
  min-width: 0;
}
.pet-details-main h2 {
  margin: 0 0 1rem 0;
  font-size: 1.4rem;
}
.pet-detail-info {
  font-size: 1.07rem;
  color: var(--text-secondary, #374151);
  margin-bottom: 5px;
}
.pet-detail-status-row {
  margin-top: 1.5rem;
  display: flex;
  gap: 1.2rem;
  align-items: center;
}
.pet-details-status {
  background: var(--accent, #bbb);
  color: #fff;
  border-radius: var(--radius-sm, 4px);
  font-weight: bold;
  padding: 0.39rem 1.15rem;
  font-size: 1.04rem;
}
.btn-primary, .btn-secondary {
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md, 6px);
  font-weight: 500;
  border: 1px solid var(--accent, #3b82f6);
  background: var(--accent, #3b82f6);
  color: white;
  cursor: pointer;
  transition: all 0.18s ease;
}
.btn-primary:hover {
  background: #2563eb;
}
.btn-secondary {
  background: white;
  color: var(--accent, #3b82f6);
}
.btn-secondary:hover {
  background: #e0ebfa;
}
.pet-pending-msg {
  color: #eab308;
  font-weight: 500;
}
.pet-adopted-msg {
  color: #71717a;
  font-weight: 500;
}
.error {
  color: #dc2626;
}
.empty-state {
  color: var(--text-muted, #999);
  text-align: center;
  padding: 1.5rem 0 1rem;
}
@media (max-width: 768px) {
  .pet-details-card {
    flex-direction: column;
    align-items: stretch;
    gap: 1.5rem;
  }
}
