/* Modern Pet Listing Styles */
.pet-listing-container {
  max-width: 900px;
  margin: 2rem auto;
  background: var(--bg-white, #fff);
  padding: 2rem;
  border-radius: var(--radius-xl, 12px);
  box-shadow: var(--shadow-md, 0 4px 6px rgba(0,0,0,0.08));
}
.pet-filter-bar {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
  justify-content: flex-start;
}
.filter-dropdown {
  margin-left: 1rem;
  padding: 0.4rem 1rem;
  border-radius: var(--radius-md, 6px);
  border: 1px solid var(--border-light, #ddd);
}
.pet-list {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}
.pet-card {
  background: var(--bg-gray-50, #f9fafb);
  border-radius: var(--radius-lg, 8px);
  box-shadow: var(--shadow-sm, 0 1px 2px rgba(0,0,0,0.04));
  padding: 1.2rem 1rem;
  flex: 0 0 265px;
  min-width: 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.pet-card-name > h2 {
  margin: 0;
  font-size: 1.3rem;
  color: var(--text-primary, #1f2937);
}
.pet-card-info > div {
  color: var(--text-secondary, #374151);
  font-size: 1rem;
  margin-bottom: 0.2rem;
}
.pet-card-label {
  font-weight: 600;
  color: var(--text-muted, #6b7280);
}
.pet-card-status-action {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}
.pet-card-status {
  display: inline-block;
  color: white;
  border-radius: var(--radius-sm, 4px);
  font-weight: 600;
  font-size: 0.97rem;
  padding: 0.42rem 1.1rem;
  margin-right: 0.2rem;
  text-transform: capitalize;
}
.btn-primary {
  background: var(--accent, #3b82f6);
  color: white;
  border: 1px solid var(--accent, #3b82f6);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md, 6px);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn-primary:hover {
  background: rgba(59,130,246,0.92);
  transform: translateY(-1px);
}
.error {
  color: #dc2626;
  margin: 1rem 0;
}
.empty-state {
  color: var(--text-muted, #999);
  text-align: center;
  font-style: italic;
  padding: 1.5rem 0 1rem;
}
@media (max-width: 600px) {
  .pet-list {
    flex-direction: column;
    gap: 1.4rem;
  }
  .pet-card {
    width: 100%;
    flex: 1 1 80vw;
  }
  .pet-filter-bar {
    flex-direction: column;
    gap: 1rem;
  }
}
